  background: #171921 0 0 / 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Kumbh Sans, sans-serif;
  font-size: 15px;
  line-height: 1.4;
  overflow-x: hidden;
}

body:before {
  content: " ";
  z-index: 999;
  pointer-events: none;
  background: linear-gradient(#171921 0%, #17192100 100%);
  height: 10vw;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
